import React, { Component } from "react";
import NavBar from "./NavBar";
import "../Styles/Create.css";
import axios from "axios";
import { connect } from "react-redux";
import {
  getBackground,
  getHead,
  getOutfit,
} from "../Redux/Reducers/pngReducer";

class Create extends Component {
  constructor() {
    super();
    this.state = {
      background: "",
      head: "",
      outfit: "",
      png:""
    };
  }
  componentDidMount() {
    this.props.getBackground();
    this.props.getHead();
    this.props.getOutfit();

    console.log(this.props.background);
  }
  getDiv = () => {
    let backid = document.getElementsByClassName("background-container")[0].id;
    this.setState({ background: backid });
    console.log(backid);
  };
  getHeadDiv = () => {
    let headid = document.getElementsByClassName("head-container")[0].id;
    this.setState({ head: headid });
    console.log(headid);
  };
  getOutfitDiv = () => {
    let outid = document.getElementsByClassName("outfit-container")[0].id;
    this.setState({ outfit: outid });
  };
  getFile =(e)=>{     
    let files = e.target.files
    console.log (files[0].name)
    this.setState({background:files[0].name})
  }
  render() {
  
    return (
      <div className="create">
        <NavBar />
        <div className="create-container">
          <div className="name-container">
            <p>Colletion Name</p>
            <div className="layerOrder-container">
              <p>Layer Orders</p>
            </div>
          </div>
          <div className="layers">
            <h1>background</h1>
            <div className="back">
              <div className="map">
                {this.props.background.map((back) => {
                  return (
                    <div
                      id={back.png}
                      className="background-container"
                      onClick={this.getDiv}
                    >
                      <img className="background-img" src={back.png} />
                      <p>{back.name}</p>
                    </div>
                  );
                })}
              </div>
              <input type = 'file' onChange = {(e)=>this.getFile(e)} ></input>
            </div>
            <h1>Head</h1>
            <div className="head">
              {" "}
              <div className="map">
                {this.props.head.map((head) => {
                  return (
                    <div
                      id={head.png}
                      className="head-container"
                      onClick={this.getHeadDiv}
                    >
                      <img className="head-img" src={head.png} />
                      <p id="p-">{head.name}</p>
                    </div>
                  );
                })}
              </div>
              <input type = 'file'/>
            </div>
            <h1>Outfit</h1>
            <div className="body">
              {" "}
              <div className="map">
                {this.props.outfit.map((outfit) => {
                  return (
                    <div
                      id={outfit.png}
                      className="outfit-container"
                      onClick={this.getOutfitDiv}
                    >
                      <img className="head-img" src={outfit.png} />
                      <p id="p-">{outfit.name}</p>
                    </div>
                  );
                })}
              </div>
            <input type = 'file'/>
            </div>
          </div>
          <section className="nft-container">
            <div className="connect">
              <h1>connect wallet</h1>
            </div>
            <div className="nft">
              <div>
              <img id="nft-png1" src={this.state.background}></img>

              <img id="nft-png2" src={this.state.head}></img>
              <img id="nft-png3" src={this.state.outfit}></img>
              </div>
            </div>
          </section>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (reduxState) => {
  return {
    background: reduxState.pngReducer.background,
    head: reduxState.pngReducer.head,
    outfit: reduxState.pngReducer.outfit,
  };
};
export default connect(mapStateToProps, {
  getBackground,
  getHead,
  getOutfit,
})(Create);
